<template>
  <div class="manage">
    <div class="man-info">
      <m-info></m-info>
    </div>
    <div class="man-list">
      <!-- title -->
      <div class="z-flex z-align-item-center title">
        <span>賬號管理</span>
      </div>
      <!-- 筛选 -->
      <div class="flit z-flex z-justify-content-between">
        <m-select v-model="choosedFactory" :subList="factoryList" :label="'廠區'"></m-select>
        <m-select v-model="choosedBusiness" :subList="businessList" :label="'事業群'"></m-select>
        <m-select v-model="choosedProduct" :subList="producList" :label="'產品處'"></m-select>

        <!-- <div
          v-for="(flitItem, flitIndex) in flitList"
          :key="flitItem.id"
          class="seletAdminManage z-wid100"
          :class="flitItem.active ? 'active' : ''"
          @click="handleFlitTypeClick(flitIndex)"
        >
          {{ flitItem.scope }}:{{ flitItem.checkedItem.txt }}
          <div class="flitItemWrapper">
            <p
              v-for="(subItem, subIndex) in flitItem.sub"
              :key="subItem.id"
              @click="handleFlitItemClick(subIndex)"
              :class="subItem.id == flitItem.checkedItem.id ? 'active' : ''"
            >
              {{ subItem.txt }}
            </p>
          </div>
        </div> -->
      </div>
      <!-- 表格 -->
      <div class="table">
        <m-table @choosedData="getChoosedData" />
      </div>
      <!-- 彈窗 -->
      <div>
        <details-alert
          :choosedData="choosedData"
          @choosedDataEmpty="setChoosedData"
        />
      </div>
      <!-- 刪除按鈕 -->
      <div class="delBtn">
        <svg-btn
          class="svgBtn"
          :inColor="delBtn.inColor"
          :outColor="delBtn.outColor"
          :fillColor="delBtn.fillColor"
        />
        <span>刪除</span>
      </div>
    </div>
  </div>
</template>

<script>
import MInfo from "./Modules/Info";
import MTable from "./Modules/Table";
import DetailsAlert from "./Modules/DetailsAlert";
import SvgBtn from "@/components/operate/SvgBtn";
import MSelect from "@/components/operate/Select";

export default {
  data() {
    return {
      choosedFactory: '',
      choosedBusiness: '',
      choosedProduct: '',
      // 廠區
      factoryList: [
        { value: "all", label: "全部" },
        { value: "cd", label: "成都" },
        { value: "sz", label: "深圳" }
      ],
      // 事業群
      businessList:[
        {value: 'all', label:'全部'},
        {value: 'mcebg', label:'Mcebg'},
      ],
      // 產品處
      producList:[
        {value: 'all', label:'全部'},
        {value: 'Mac(I)', label:'Mac(I)'},
      ],
      // 選中的表格數據
      choosedData: {},
      // 刪除按鈕的顏色
      delBtn: {
        inColor: "#5B6573",
        outColor: "#485463",
        fillColor: "#485463"
      },
      // 篩選欄
      flitList: [
        {
          scope: "廠區",
          val: "factory",
          id: "1",
          active: false,
          checkedItem: { txt: "全部", val: "all", id: "1" },
          sub: [
            { txt: "全部", val: "all", id: "1" },
            { txt: "CD", val: "cd", id: "2" },
            { txt: "SZ", val: "sz", id: "3" }
          ]
        },
        {
          scope: "事業群",
          val: "business",
          id: "2",
          active: false,
          checkedItem: { txt: "全部", val: "all", id: "1" },
          sub: [
            { txt: "全部", val: "all", id: "1" },
            { txt: "SHZBG", val: "SHZBG", id: "2" },
            { txt: "MCEBG", val: "MCEBG", id: "3" }
          ]
        },
        {
          scope: "產品處",
          val: "product",
          id: "3",
          active: false,
          checkedItem: { txt: "全部", val: "all", id: "1" },
          sub: [
            { txt: "全部", val: "all", id: "1" },
            { txt: "Mac(I)", val: "Mac(I)", id: "2" }
          ]
        },
        {
          scope: "類型",
          val: "type",
          id: "4",
          active: false,
          checkedItem: { txt: "全部", val: "all", id: "1" },
          sub: [
            { txt: "全部", val: "all", id: "1" },
            { txt: "發單方", val: "send", id: "2" },
            { txt: "接單方", val: "recv", id: "3" }
          ]
        }
      ]
    };
  },
  methods: {
    // 篩選item點擊事件
    handleFlitItemClick(index) {
      for (let i in this.flitList) {
        if (this.flitList[i].active == true) {
          this.flitList[i].checkedItem = this.flitList[i].sub[index];
          // handleFlitTypeClick(i)
          return;
        }
      }
    },
    // 篩選type點擊事件
    handleFlitTypeClick(index) {
      if (this.flitList[index].active == true) {
        this.flitList[index].active = false;
        return;
      }
      for (let i in this.flitList) {
        this.flitList[i].active = false;
      }
      this.flitList[index].active = true;
    },
    // 設置選中的表格數據
    setChoosedData() {
      this.choosedData = {};
    },
    // 獲取選中的表格數據
    getChoosedData(val) {
      console.log("get");
      this.choosedData = val;
    },
    // 選擇分類
    toggleFlit(val) {
      for (let item of this.flitList) {
        item.active = false;
      }
      switch (val) {
        case "all":
          this.flitList[0].active = true;
          break;
        case "recv":
          this.flitList[1].active = true;
          break;
        case "send":
          this.flitList[2].active = true;
          break;
      }
    }
  },
  components: {
    MInfo,
    MTable,
    DetailsAlert,
    SvgBtn,
    MSelect,
  }
};
</script>

<style lang="less" scoped>
.manage {
  .man-info,
  .man-list {
    background-color: #eff9ff;
    box-sizing: border-box;
    color: #12222e;
    padding: 20px;
    min-height: 100%;
    min-width: 925px;
  }
  .man-list {
    margin-top: 10px;
  }
  .title {
    > span {
      margin-right: 1rem;
    }
    > button {
      margin: 0 1rem;
    }
  }
  .delBtn {
    display: inline-block;
    position: relative;
    margin-top: 20px;
    > .svgBtn {
      transform: scaleX(0.8);
    }
    > span {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      font-size: 20px;
      color: #fff;
    }
  }
}
.flit{
  margin-top: 10px;
}
